'use strict';

/**
* Test if a value is a boolean.
*
* @module @stdlib/assert-is-boolean
*
* @example
* import Boolean from '@stdlib/boolean-ctor' ;
* import isBoolean from '@stdlib/assert-is-boolean' ;
*
* var bool = isBoolean( false );
* // returns true
*
* bool = isBoolean( new Boolean( false ) );
* // returns true
*
* @example
* import Boolean from '@stdlib/boolean-ctor' ;
* import { isPrimitive as isBoolean } from '@stdlib/assert-is-boolean' ;
*
* var bool = isBoolean( false );
* // returns true
*
* bool = isBoolean( new Boolean( true ) );
* // returns false
*
* @example
* import Boolean from '@stdlib/boolean-ctor' ;
* import { isObject as isBoolean } from '@stdlib/assert-is-boolean' ;
*
* var bool = isBoolean( true );
* // returns false
*
* bool = isBoolean( new Boolean( false ) );
* // returns true
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property' ;
import main from './main.js' ;
import isPrimitive from './primitive.js' ;
import isObject from './object.js' ;


// MAIN //

setReadOnly( main, 'isPrimitive', isPrimitive );
setReadOnly( main, 'isObject', isObject );


// EXPORTS //

export default main;
